.app__searchForm {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 15px;
}
.app__input:focus {
  background-color: bisque;
}

.app__submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  /* border-radius: 4px; */
  color: white;
  cursor: pointer;
  border-radius: 0.5rem;
}

.app__submit:hover {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

h1 {
  text-align: center;
  font-size: 3rem;
  color: rgb(221, 15, 193);
}

html {
  scroll-behavior: smooth;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.row {
  width: 100%;
}
.row::after {
  clear: both;
  content: "";
  display: table;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}

[class*="col-"] {
  width: 100%;
}

/*main food menu start */
#menu-section {
  padding: 1%;
  display: inline;
}
.card-body {
  text-align: center;
}
.card-body img {
  width: 100%;
  height: 250px;
  display: block;
}

.item-des {
  background: tomato;
}
.item-name {
  background: #954535;
  color: white;
  padding: 10px;
}
.item-des p {
  font-size: 1rem;
  padding: 5px;
  font-weight: bold;
  /* align-items: baseline; */
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.card {
  transition: 0.4s;
}
.card:hover {
  transform: scale(1.1);
}

.app__healthLabels {
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
}
/* main food menu end */

